body, html {
  justify-content: center;
  align-items: center;
  height: 100%;
  margin: 0;
  padding: 0;
  display: flex;
}

.stopwatch {
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  display: flex;
}

.stopwatch__container {
  border: 1vmin dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
}

.stopwatch__container_circle {
  z-index: 2;
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: relative;
}

.stopwatch__container_minutesHands {
  z-index: 1;
  transform-origin: bottom;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite rotate-minutes;
  position: absolute;
  bottom: 50%;
  transform: rotate(0);
}

.stopwatch__container_secondsHands {
  z-index: 1;
  transform-origin: bottom;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite rotate-seconds;
  position: absolute;
  bottom: 50%;
  transform: rotate(0);
}

@keyframes rotate-seconds {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

@keyframes rotate-minutes {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

.stopwatch--speed-up .stopwatch__container_secondsHands {
  animation-duration: 10s;
}

.stopwatch-speed-up .stopwatch__container_minutesHands {
  animation-duration: 600s;
}
